<template>
    <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">Інформація про фільм</h3>
      </div>
      <form @submit.prevent="updateFilm">
        <div class="card-body">
          <div class="form-group">
            <label for="nameFilm">Назва фільма</label>
            <input
              type="text"
              class="form-control"
              id="nameFilm"
              v-model="film.nameFilm"
              required
            />
          </div>
          <div class="form-group">
            <label for="description">Опис</label>
            <textarea
              type="text"
              class="form-control"
              id="description"
              placeholder="Текст"
              v-model="film.description"
              required
            ></textarea>
          </div>
          <div class="file-input">
              <label for="holovnaImg">Головна картинка</label>
              <input type="file" ref="fileInput" class="file-input__input" @change="handleFileChange" v-if="film.imageFirst === null" required>
              <button @click="triggerFileInput" class="btn btn-block btn-default"  v-if="film.imageFirst === null">Добавити</button>
              <div class="image-delete_btn">
                <div class="image-delete">
                  <img :src="film.imageFirst" class="imgInput" v-if="film.imageFirst">
                </div>
                <button @click="deleteImageSrc" class="btn btn-block btn-default"  v-if="film.imageFirst !== null">Видалити</button>
              </div>
          </div>
          <div class="file-input">
              <label for="holovnaImg">Галерея картинок</label>
              <input type="file" ref="fileInput2" class="file-input__input" @change="handleFileChange2" multiple>
              <button @click="triggerFileInput2" class="btn btn-block btn-default" v-if="galleryImages == []">Добавити</button>
              <div class="image-delete_btn2">
                <div class="image-delete" v-for="(galleryImg, i) in galleryImages" :key="i">
                  <img :src="galleryImg" class="imgInput" v-if="galleryImg">
                  <span class="deleteSpan" v-if="galleryImg" @click="deleteGalleryImg(i)">x</span>
                </div>
                <button @click="triggerFileInput2" class="btn btn-block btn-default"  v-if="galleryImages != []">Добавити</button>
              </div>
          </div>
          <div class="form-group">
            <label>Посилання на трейлер</label>
            <input
              type="text"
              class="form-control"
              placeholder="Введіть посилання на трейлер"
              v-model="film.linkTrailer"
            />
          </div>
          <div class="form-group">
            <label>Тип фільма</label>
            <div class="form-main">
              <div class="form-check" v-for="(typeFilm, i) in typeFilms" :key="i" @click="pushTypeFilm(typeFilm, i)">
                <input class="form-check-input" v-model="typeFilm.checked" type="checkbox" :id="typeFilm.type">
                <label class="form-check-label" :for="typeFilm.type">{{ typeFilm.type }}</label>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label>Фільм</label>
            <div class="form-main">
              <div class="form-check" v-for="(dataFilm, i) in dataFilms" :key="i">
                <input class="form-check-input" type="radio"
                 :id="dataFilm.name" name="name" :value="dataFilm.name" v-model="radioData">
                <label class="form-check-label" :for="dataFilm.name">{{ dataFilm.name }}</label>
              </div>
            </div>
          </div>
          <div class="form-group">
            <h5>SEO блок:</h5>
            <label>URL</label>
            <input
              type="text"
              class="form-control"
              placeholder="URL"
              v-model="film.url"
            />
          </div>
          <div class="form-group">
            <label>Title</label>
            <input
              type="text"
              class="form-control"
              placeholder="Title"
              v-model="film.title"
            />
          </div>
          <div class="form-group">
            <label>Keywords</label>
            <input
              type="text"
              class="form-control"
              placeholder="word"
              v-model="film.keywords"
            />
          </div>
          <div class="form-group">
            <label>Description</label>
            <textarea
              type="text"
              class="form-control"
              placeholder="Description"
              v-model="film.descriptionSeo"
            >
            </textarea>
          </div>
        </div>  
        <div v-if="showMessage" class="message">
          {{ message }}
        </div>
        <div class="card-footer">
          <button @click="updateFilm" class="btn btn-primary" type="submit">Оновити</button>
          <button class="btn btn-primary" @click="resetFilm" type="button">Повернути базову версію</button>
        </div>
      </form>
    </div>
  </div>
</template>


<script>

import { ref, onMounted   } from 'vue'
import { getDatabase, ref as dbRef, onValue, update, remove } from 'firebase/database'
import { getStorage, ref as storRef, uploadBytes, getDownloadURL, deleteObject, listAll } from 'firebase/storage'

export default {

    // setup() {
    //   const film = ref({})
    //   const vm = this

    //   onMounted(async () => {
    //     const movieId = vm.$route.params.id
    //     const db = getDatabase()
    //     const movieRef = dbRef(db, `films/${movieId}`)
    //     const snapshot = await get(movieRef)
    //     film.value = snapshot.val()
    //   })

    //   return {
    //     film
    //   }
    // }
  

  data() {
    return {
      film: {},
      originalFilm: {},
      filmId: null,
      typeFilms: [
        {type: '2D', checked: false},
        {type: '3D', checked: false},
        {type: 'IMAX', checked: false},
      ],
      dataFilms: [
        {name: 'Вже вийшов', checked: false},
        {name: 'Скоро вийде', checked: false}
      ],
      fileImgSrc: null,
      message: 'Фільм збережено!',
      showMessage: false,
      fileGalleryImg: [],
      imageSrc2: null,
      galleryImages: [],
      typeFilm: [],
      radioData: '',
      loaded: false,
    };
  },
  async mounted() {
    await this.getFilm();
  },
  methods: {
    async getFilm() {
      const db = getDatabase()
      this.filmId = this.$route.params.id
      const filmRef = dbRef(db, 'films/' + this.filmId);
      await new Promise((resolve) => {
        onValue(filmRef, (snapshot) => {
          if (snapshot.exists()) {
            this.film = snapshot.val();
            this.originalFilm = snapshot.val();
            console.log(this.originalFilm)
            this.galleryImages = this.film.galleryImages
          } else {
            console.log('Фільм не знайдено');
          }
          resolve();
        });
      });
      this.updateChecked()
      this.updateRadio() 
    },

    updateChecked() {
      this.typeFilms.forEach(item => {
        if (this.film.typeFilm.includes(item.type)) {
          item.checked = true;
          if(item.checked === true && !this.typeFilm.includes(item.type)){
            this.typeFilm.push(item.type)
          }
        }
      });
    },

    pushTypeFilm(typeFilm, i){
      if(!this.typeFilm.includes(typeFilm.type) && typeFilm.checked === false){
        this.typeFilm.push(typeFilm.type)
        console.log(this.typeFilm)
      } else if(this.typeFilm.includes(typeFilm.type) && typeFilm.checked === true){
        this.typeFilm.forEach(function (item, index, typeFilms) {
          if (typeFilm.type === item) {
            i = index;
          }
        });
        this.typeFilm.splice(i, 1)
      }
    },

     updateRadio() {
      this.dataFilms.forEach(item => {
        if (this.film.radioData.includes(item.name)) {
          item.checked = true;
          if(item.checked === true){
            this.radioData = item.name
          }
        }
      });
    },

    //Головна картинка
    handleFileChange(event){
      this.fileImgSrc = event.target.files[0];
      this.film.imageFirst = URL.createObjectURL(this.fileImgSrc);
    },
    triggerFileInput(){
      this.$refs.fileInput.click();
    },
    deleteImageSrc(){
      this.film.imageFirst = null
    },


    //Галерея картинок

    handleFileChange2(event) {
      this.fileGalleryImg = [...event.target.files]
      for(let image of this.fileGalleryImg){
        this.imageSrc2 = URL.createObjectURL(image)
        this.galleryImages.push(this.imageSrc2)
      }
    },
    triggerFileInput2(){
      this.$refs.fileInput2.click();
    },
    deleteGalleryImg(i){
      this.galleryImages.splice(i, 1)
    },



    updateFilm(){
      const db = getDatabase()
      const storage = getStorage()

      const pathDb = `films/${this.filmId}`
      const pathStor = `films/${this.filmId}/galleryImgs`

      const movieRef = dbRef(db, pathDb);

      // Оновіть дані фільму в базі даних
      update(movieRef, {
        id: movieRef.key,
        nameFilm: this.film.nameFilm,
        description: this.film.description,
        //imageFirst: this.fileImgSrc,
        // galleryImgs: this.fileGalleryImg,
        linkTrailer: this.film.linkTrailer, 
        typeFilm: this.typeFilm,
        radioData: this.radioData,
        url: this.film.url,
        title: this.film.title, 
        keywords: this.film.keywords,
        descriptionSeo: this.film.descriptionSeo
      });

      if(this.fileImgSrc){
        update(movieRef, {
          imageFirst: this.fileImgSrc,
        })
      } else{
        update(movieRef, {
          imageFirst: this.film.imageFirst,
        })
      }


      // if(this.galleryImages.length != this.film.galleryImages.length){

      //   update(movieRef, {
      //     galleryImgs: this.fileGalleryImg,
      //   })
      // }
      // Завантажити картинки галереї до Firebase Storage та зберегти посилання на них до фільму в Firebase Realtime Database
      const galleryImages = [];

        this.fileGalleryImg.forEach((galleryImg, index) => {
          const storageRef = storRef(storage, `films/${movieRef.key}/galleryImgs/${index}`);
          uploadBytes(storageRef, galleryImg).then((snapshot) => {
            getDownloadURL(snapshot.ref).then((url) => {
              galleryImages[index] = url;
              if (galleryImages.length === this.fileGalleryImg.length) {
                update(movieRef, {
                  galleryImages
                });
              }
            });
          });
        })


         // Завантажити головну картинку до Firebase Storage та зберегти посилання на неї до фільму в Firebase Realtime Database
         if (this.fileImgSrc) {
        const storageRef = storRef(storage, `films/${movieRef.key}/imageFirst`);
        uploadBytes(storageRef, this.fileImgSrc).then((snapshot) => {
          getDownloadURL(snapshot.ref).then((url) => {
            update(movieRef, {
              imageFirst: url
            });
          });
        });
      }

      // Logic to save the film goes here
      this.showMessage = true
      setTimeout(() => {
        this.showMessage = false
      }, 3000)

      this.$router.push('/films')


    },

    resetFilm(){
      this.film.description = this.originalFilm.description
      this.film.descriptionSeo = this.originalFilm.descriptionSeo
      this.film.galleryImages = this.originalFilm.galleryImages
      this.film.imageFirst = this.originalFilm.imageFirst
      this.film.keywords = this.originalFilm.keywords
      this.film.linkTrailer = this.originalFilm.linkTrailer
      this.film.nameFilm = this.originalFilm.nameFilm
      this.radioData = this.originalFilm.radioData
      this.film.title = this.originalFilm.title
     // this.typeFilms = this.removeTypeFilms()
      this.typeFilms.forEach(item => {
        item.checked = this.originalFilm.typeFilm.includes(item.type)
      });
      this.film.url = this.originalFilm.url
    },
  },
} 
</script>
<style lang="scss">

.container{
    width: 70%;
}
// input[type="file"] {
//   display: block;
//   opacity: 1;  
// }

.upload-button {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
}

.upload-preview {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin-left: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
label{
  display: block;
  text-align: left;
}
.file-input {
  position: relative;
}
.file-input__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 30%;
  height: 70px;
  cursor: pointer;
}
.btn-block {
    display: block;
    width: 30%;
}
.imgInput{
  width: 250px;
  height: 250px;
  display: block;
}
.image-delete_btn{
  display: flex;
  gap: 40px;
  align-items: end;
  margin-bottom: 0.5rem;
}
.image-delete_btn2{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: end;
  margin-bottom: 0.5rem;
}
.deleteSpan{
  position: absolute;
  top: -8px;
  right: -8px;
  border: 1px solid black;
  background: white;
  border-radius: 50%;
  color: black;
  width: 26px;
  cursor: pointer;
}
.image-delete{
  position: relative;
}
.form-main{
  display: flex;
  gap: 20px;
}
.form-check-input {
  display: block;
}
.form-group{

  h5{
    text-align: start;
  }
}
.card-footer{
  display: flex;
  gap: 30px;
  justify-content: center;
}

                                          /// MESSAGE

.message {
  position: fixed;
  bottom: 100px;
  right: -40px;
  transform: translateX(-50%);
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 10px;
}
    
</style>